//action
import {
    UPDATE_LIST_CONFIGS,
    CHANGE_LIST_CHECKBOX,
    GET_CLASSIFY_LIST,
    GET_CLASSIFY_INFO,
    POST_CLASSIFY_INFO,
    DELETE_CLASSIFY,
    UPDATE_CLASSIFY_STATE
} from '../constants'


//
const classifyInitialState = {
    isFetching: 0,
    error: 0,
    message: '',
    tools: [
        {
            text: '',
            icon: 'icon-close'
        }
    ],
    list: [], //
    info: null, //()
    count: 0, //
    //
    configs:{
        listPath: 'classify',
        page: 1, //
        limit: 20, //
        searchMode: 0, //
        checkboxs: true, // 0->, 1->
        checked: false, //false->, all->, []->
        search: '',
        order: [],
        column: [{
            key: 'id',
            title: '',
            order: true,
            visibility: true,
            width: 60,
            resize: 0
        },{
            key: 'name',
            title: '',
            order: true,
            visibility: true,
            width: 200,
            resize: 1
        },{
            key: 'slug',
            title: '',
            order: true,
            visibility: true,
            width: 200,
            resize: 1
        },{
            key: 'type',
            title: '',
            order: true,
            visibility: true,
            width: 120,
            resize: 1
        }]
    }
}

export function classify(state = classifyInitialState, action) {

    if (action.path !== "classify") {
        return state
    }

    let ids = [], oldlist = []

    //action typestate
    switch (action.type) {
        case GET_CLASSIFY_LIST:
            action.payload.configs = {
                ...state.configs,
                ...action.payload.configs
            }
            return {
                ...state,
                ...action.payload
            }
        case UPDATE_LIST_CONFIGS:
            let configs = {...state.configs, ...action.payload}
            return {...state, ...{configs}}
        case CHANGE_LIST_CHECKBOX:
            let list = [...action.payload]
            return {...state, list:[...list]}
        case GET_CLASSIFY_INFO:
            return {...state, info: action.payload.info}
        case POST_CLASSIFY_INFO:
            return {...state, ...action.payload}
        case DELETE_CLASSIFY:
            ids = action.payload.ids
            oldlist = state.list
            if (ids) {
                for (let i = 0; i < ids.length; i++) {
                    for (let k = 0; k < oldlist.length; k++) {
                        if (ids[i] == oldlist[k].id) {
                            oldlist.splice(k, 1)
                        }
                    }
                }
            }
            return {...state, ...action.payload, list: [...oldlist]}
        case UPDATE_CLASSIFY_STATE:
            ids = action.payload.ids
            oldlist = state.list
            if (ids) {
                for (let i = 0; i < ids.length; i++) {
                    for (let k = 0; k < oldlist.length; k++) {
                        if (ids[i] == oldlist[k].id) {
                            oldlist[k].state = action.payload.state
                        }
                    }
                }
            }
            return {...state, ...action.payload, list: [...oldlist]}
        default:
            return {...state}
    }
}
