import {
    LOGIN,
    LOGOUT,
    GET_AUTH_INFO
} from '../constants'


//
const commonInitialState = {
    logined: 0,
    renew_csrf_token: 0,
    isFetching: 0,
    error: 0,
    version: '10.0.106',
    logo: '/public/images/photo.jpg',
    logoname: "alvin's blog",
    message: '',
    classifyList: [{
        id: 0,
        name: ''
    },{
        id: 1,
        name: ''
    }],
    roleList: [{
        id: 0,
        name: '',
    },{
        id: 1,
        name: '',
    },{
        id: 2,
        name: ''
    },{
        id: 3,
        name: ''
    },{
        id: 4,
        name: ''
    }],
    userStateList: [{
        id: 0,
        name: ''
    },{
        id: 1,
        name: ''
    },{
        id: 2,
        name: ''
    }],
    commentStateList: [{
        id: 0,
        name: ''
    },{
        id: 1,
        name: ''
    }],
}

export function common(state = commonInitialState, action) {

    if (action.path !== "common") {
        return state
    }

    //action typestate
    switch (action.type) {
        case LOGIN:
            return {...state, ...action.payload}
        case LOGOUT:
            return {...state, ...action.payload}
        case GET_AUTH_INFO:
            return {...state, ...action.payload}
        // case GET_OU_IN_COMPONENT:
        //     return {...state, ...action.payload}
        default:
            return { ...state }
    }
}
