//action
import {
    UPDATE_LIST_CONFIGS,
    CHANGE_LIST_CHECKBOX,
    GET_USER_LIST,
    GET_USER_INFO,
    POST_USER_INFO,
    DELETE_USER,
    UPDATE_USER_STATE
} from '../constants'


//
const userInitialState = {
    isFetching: 0,
    error: 0,
    message: '',
    tools: [
        {
            text: '',
            icon: 'icon-close'
        },{
            text: '',
            icon: 'user-following'
        },{
            text: '',
            icon: 'user-following'
        }
    ],
    list: [], //
    info: null, //()
    count: 64, //
    //
    configs:{
        listPath: 'user',
        page: 1, //
        limit: 20, //
        searchMode: 0, //
        checkboxs: true, // 0->, 1->
        checked: false, //false->, all->, []->
        search: '',
        order: [],
        column: [{
            key: 'photo',
            title: '',
            order: true,
            visibility: true,
            width: 120,
            resize: 1
        },{
            key: 'name',
            title: '',
            order: true,
            visibility: true,
            width: 300,
            resize: 1
        },{
            key: 'email',
            title: '',
            order: true,
            visibility: true,
            width: 240,
            resize: 1
        },{
            key: 'type',
            title: '',
            order: true,
            visibility: true,
            width: 120,
            resize: 1
        },{
            key: 'state',
            title: '',
            order: true,
            visibility: true,
            width: 120,
            resize: 1
        }]
    }
}

export function user(state = userInitialState, action) {

    if (action.path !== "user") {
        return state
    }

    let ids = [], oldlist = []

    //action typestate
    switch (action.type) {
        case GET_USER_LIST:
            action.payload.configs = {
                ...state.configs,
                ...action.payload.configs
            }
            return {
                ...state,
                ...action.payload
            }
        case UPDATE_LIST_CONFIGS:
            let configs = {...state.configs, ...action.payload}
            return {...state, ...{configs}}
        case CHANGE_LIST_CHECKBOX:
            let list = [...action.payload]
            return {...state, list:[...list]}
        case GET_USER_INFO:
            return {...state, info: action.payload.info}
        case POST_USER_INFO:
            return {...state, ...action.payload}
        case DELETE_USER:
            ids = action.payload.ids
            oldlist = state.list
            if (ids) {
                for (let i = 0; i < ids.length; i++) {
                    for (let k = 0; k < oldlist.length; k++) {
                        if (ids[i] == oldlist[k].id) {
                            oldlist.splice(k, 1)
                        }
                    }
                }
            }
            return {...state, ...action.payload, list: [...oldlist]}
        case UPDATE_USER_STATE:
            ids = action.payload.ids
            oldlist = state.list
            if (ids) {
                for (let i = 0; i < ids.length; i++) {
                    for (let k = 0; k < oldlist.length; k++) {
                        if (ids[i] == oldlist[k].id) {
                            oldlist[k].state = action.payload.state
                        }
                    }
                }
            }
            return {...state, ...action.payload, list: [...oldlist]}
        default:
            return {...state}
    }
}
